<script>
import axios from "axios";
import PostListMain from "@/components/PostListMain.vue";

export default {
  components: {
    PostListMain,
  },
  data() {
    return {
      posts: [],
      postLoading: false,
      selectedSort: "",
      searchQuery: "",
      page: 1,
      limit: 20,
      totalPage: 0,
    };
  },
  methods: {
    async fetchBeer() {
      console.log("fetching");
      try {
        this.postLoading = true;
        const res = await axios.get("https://api.punkapi.com/v2/beers", {
          params: {
            page: this.page,
            per_page: this.limit,
          },
        });
        this.posts = res.data;
      } catch (e) {
        console.error(e);
      } finally {
        this.postLoading = false;
      }
    },
  },
  mounted() {
    this.fetchBeer();
  },
  computed: {},
  watch: {},
};
</script>

